}

.ubadge:hover {
	box-shadow: 0px 5px 5px #0d659d;
	box-shadow: 0px 5px 5px var(--colscheme3);
	transform: scale(1.1);
}

.sec-most-played {
	display: flex;
	flex-wrap: wrap;
}

.game {
	min-width: 200px;
	flex-grow: 1;

	height: 100px;
	margin: 20px;

	background: #FFFFFF90;
	border-radius: 20px;
	text-align: center;
	transition: all 0.1s ease-out;
	display: flex;
	cursor: pointer;
}

.game h5 {
	margin: auto;
}
.game:hover {
	transform: scale(1.05);
	box-shadow: 0px 5px 5px #0d659d;
	box-shadow: 0px 5px 5px var(--colscheme3);
}

.frn {
	height: 70px;
	background: #FFFFFF90;
	padding: 0 10px;
	display: flex;
	justify-content: center;
	margin: 10px;
	transition: all 0.1s ease-out;
	position: relative;
}

.frn:hover {
	cursor: pointer;
	text-decoration: underline;
	box-shadow: 0px 5px 5px #0d659d;
	box-shadow: 0px 5px 5px var(--colscheme3);

}
.frn:nth-child(even) {
	background:#6DA7E550;
}
.frn-img {
	background: #FFFFFF90;
	border-radius: 50%;
	width: 50px;
	height: 50px;
	margin: auto 5px;
}

.frn h5 {
	margin: auto;
}
h4 {
	flex-basis: 100%;
	padding: 20px;
	font-size: 30px;
	color: white;
	background: linear-gradient(#6bcae2, #c7d8e7, #ffffff);
	background: linear-gradient(var(--colscheme2), var(--colscheme4), #ffffff);
}

.bio{
	color:Blue;
	border: 1px solid Blue;
}

@media (max-width: 1340px) {

	#user-pfp {
		width: 200px;
		height: 200px;
	}
	#user-infosec {
		margin-right: auto;
	}
}

@media (max-width: 1200px) {
	#circle-stats {
		display: none;
	}
	.profile-div-left {
		flex-basis: 100%;
		margin: 0;
		padding: 0;
		margin-bottom: 25px;
	}
	.profile-div-right {
		flex-basis: 100%;
		margin: 0;
	}
	.sec-friends {
		display: flex;
		flex-wrap: wrap;
	}
	.frn {
		flex-basis: 32%;
		margin: 5px;
	}
}
@media (max-width: 1000px) {
	.frn {
		flex-basis: 48%;
		margin: 5px;
	}
}
@media (max-width: 900px) {

	#user-pfp {
		margin: auto;
	}
	#user-infosec {
		margin: auto;
		padding: 20px 50px;
	}
}
@media (max-width:650px) {
	.frn {
		flex-basis: 100%;
		margin: 5px;
	}
	.ubadge {
		margin: 10px;
	}
}

